*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "oswald";
  src: url("../fonts/oswald/oswald.woff") format("woff"), url("../fonts/oswald/oswald.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "lato";
  src: url("../fonts/lato/lato.woff") format("woff"), url("../fonts/lato/lato.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 110px;
  }
}
.page {
  overflow-x: hidden;
}
.page__body {
  font-family: "oswald", "Arial", "Helvetica", sans-serif;
