  display: flex;
  align-items: center;
  color: #000000;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
  margin-top: -1px;
}
.catalogs__title--slim::before {
  content: url("../img/cat_slim.svg");
  margin-right: 20px;
  margin-top: 3px;
}
.catalogs__title--pro::before {
