  padding-bottom: 21px;
  border-bottom: 1px solid #d9d9d9;
}
.catalogs__description--line-bottom {
  border-bottom: 1px solid #d9d9d9;
}
.catalogs__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  display: flex;
  gap: 15px;
  text-align: center;
  font-family: "oswald", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}
.catalogs__link::after {
  content: "";
  background-image: url("../img/catalogs-arrow.svg");
  transition: background-image 0.5s ease;
  background-repeat: no-repeat;
  background-size: cover;
  width: 32px;
  height: 12px;
}
.catalogs__link:hover::after {
  content: "";
  background-image: url("../img/long-catalogs-arrow.svg");
  transition: background-image 0.5s ease;
  background-repeat: no-repeat;
  background-size: cover;
  width: 37px;
  height: 12px;
}
.catalogs__link:active::after,
.catalogs__link:active {
  opacity: 0.3;
}
